import styled from 'styled-components/macro';
import {mount} from '../store';
import {toggleOnProp} from '../utilities';
import {colors, spacing, font} from '../config/styles';
import GitHubLogo from '../assets/img/github.svg?include';

export default mount(
    function() {
        return (
            <StyledNavigation>
                <Anchor
                    href="https://github.com/sidofc"
                    title="Sidney Liebrand's GitHub profile"
                >
                    <StyledGitHubLogo dangerouslySetInnerHTML={{__html: GitHubLogo}} />
                </Anchor>
            </StyledNavigation>
        );
    },
    {location: ['location']}
);

const StyledGitHubLogo = styled.div`
    height: 100%;
    display: flex;
    align-items: center;

    svg {
        height: 60%;
    }
`;

const StyledNavigation = styled.nav`
    height: 100%;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
`;

const Anchor = styled.a`
    font-weight: ${font.weights.medium};
    position: relative;
    text-decoration: none;
    padding: 0 ${spacing.medium};
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    user-select: none;
    font-size: 1rem;
    cursor: ${toggleOnProp('active', 'default', 'pointer')};
    pointer-events: ${toggleOnProp('active', 'none', 'all')};
    ${colors.withVar('black', 'color')};
    outline: none;

    // &:not(:active):focus {
    ${colors.withVar('primary', 'background-color')}
    // }

    &:first-child {
        margin-left: 0;
    }

    &:last-child {
        margin-right: 0;
    }
`;
